.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-image: url('../public/1335.jpg');
  background-size: 1000px;
}

#punainen {
  font-family: 'MountainsOfChristmas';
  color: rgb(179, 2, 2);
}

#otsikko {
  font-family: 'MountainsOfChristmas';
  color: rgb(179, 2, 2);
  font-size: 22px;
  font-weight: bold;
}

#numero {
  font-family: 'MountainsOfChristmasB', cursive;
  color: rgb(0, 0, 0);
  font-size: 3em;
  margin: 0%;
}

#numeroharmaa {
  font-family: 'MountainsOfChristmasB', cursive;
  color: rgb(123, 123, 123);
  font-size: 3em;
  margin: 0%;
  padding: 10%;
}

@font-face {
  font-family: 'MountainsOfChristmasB';
  src: local('MountainsOfChristmasB'), url(./fonts/MountainsofChristmas-Bold.ttf) format('opentype');
}

@font-face {
  font-family: 'MountainsOfChristmas';
  src: local('MountainsOfChristmas'), url(./fonts/MountainsofChristmas-Regular.ttf) format('opentype');
}

iframe {
  width: 540px;
  height: 306px;
}

@media only screen and  (max-width: 660px) {
  iframe {
    width: 350px;
    height: 192px;
  }
}

@media only screen and  (max-width: 466px) {
  iframe {
    width: 256px;
    height: 144px;
  }
}